.redes-sociales {
    margin-bottom: $separacion;
    
    @include tablet {
        @include grid(2, $separacion)
    }

    .facebook {
        margin-bottom: $separacion;
        .logo-facebook {
            width: 50%;
            margin: 0 auto 2rem auto;
        }
        .fb-page {
            display: flex;
            justify-content: center;
            margin-bottom: 2.5rem;
        }
    }

    .instagram {
        .logo-instagram {
            width: 50%;
            margin: 0 auto 2rem auto;
        }
    }
}