.navegacion {
    grid-column: 1 / 5;
    display: flex;
    flex-direction: column;
    align-items: center;
    visibility: hidden;
    opacity: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height .5s linear;
    height: 0;

    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        width: 90%;
        margin: 0 auto;
        max-width: 120rem;
        opacity: 1;
        visibility: visible;
    }

    a {
        color: $blanco;
        transition: .2s ease-in-out;

        &:hover {
            color: $rojo;
            font-weight: $black;
            transform: scale(1.2);
            text-transform: uppercase;

            @include tablet {
                transform: scale(.8);
            }
        }
    }
}

.mobile-menu {
    grid-column: 4 / 5;
    display: flex;
    align-items: flex-end;

    @include tablet {
        display: none;
    }

    img {
        width: 5rem;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}