.blog {
    margin: $separacion 0;
    background-image: url(../img/fondo_1.jpg);
    background-repeat: no-repeat;
    background-position: center left;
    background-size: cover;
    -webkit-box-shadow: 0px 5px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 5px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 0px 5px 5px 0px rgba(0,0,0,0.75);
    border-radius: .5rem;

    @include desktop {
        margin-top: 0;
    }

    h2 {
        font-family: $abril;
        color: $rojo;
    }
    
    .entradas {
        @include tablet {
            width: 90%;
            margin: 0 auto;
            @include grid(2, $separacion);
        }

        .entrada-blog {
            margin-bottom: $separacion;
            background-color: $blanco;

            @include tablet {
                margin-bottom: 0;
                border: 1px solid rgba($color: $blanco, $alpha: .3);
                height: max-content;
            }

            .imagen {
                width: 90%;
                margin: .5rem auto 0 auto;
            }
            
            .contenido-blog {
                color: $grisOscuro;

                h3, p {
                    @include tablet {
                        padding: 0 1rem;
                    }
                }

                h3 {
                    @include tablet {
                        font-size: 2.2rem;
                        color: $rojo;
                    }
                }

                p {
                    font-family: $lato;
                    font-size: 1.5rem;

                    @include tablet {
                        font-size: 1.5rem;
                        line-height: 1.2;
                    }
                }

                a {
                    width: 80%;
                    margin: 2rem auto;
                }
            }
        }
    }
}

.page-blog {
    margin: 0;
    border-radius: 0;

    .entradas {
        padding-bottom: $separacion;

        @include desktop {
            @include grid(3, $separacion);
        }
    }
}