.site-header {
    .contenedor {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;

        .barra {
            @include grid(4, 0);

            a {
                grid-column: 1 / 4;

                @include tablet {
                    grid-column: 1 / 5;
                }
            }
            
            img {
                width: 50%;
                margin: $separacion auto 0 auto;
            }
        }
    }

    &.index {
        background-image: url(../img/cabecera_r&b.jpg);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        height: auto;
        margin-bottom: $separacion;

        .texto-header {
            color: $blanco;
            padding: $separacion 0;

            h2 {
                font-family: $abril;
                border-top: 1px solid $blanco;
                border-bottom: 1px solid $blanco;
                width: max-content;
                margin: 0 auto;
            }
        }
    }

    &.general {
        background-color: $negro;

        .contenedor {
            padding-bottom: 2rem;

            @include tablet {
                padding: 0;
            }

            .barra {
                @include tablet {
                    display: flex;
                    align-items: center;
                }

                img {
                    margin-bottom: $separacion;
                }
            }
        }
    }
}