.site-footer {
    background-color: $negro;
    
    .barra {
        padding: $separacion 0;

        @include desktop {
            display: flex;
        }
        
        img {
            width: 30%;
            margin: 0 auto;

            &:hover {
                @include desktop {
                    transform: scale(1.2);
                }
            }
        }

        .navegacion {
            visibility: visible;
            opacity: 1;
            height: auto;
        }
    }

    .copyright {
        color: $blanco;
        font-family: $lato;
        text-transform: uppercase;
        font-size: 1.5rem;
    }
}