.servicios {
    h2 {
        font-weight: $black;

    }

    h3 {
        font-weight: $bold;
        border-bottom: 2px solid $gris;
        margin-bottom: 2rem;
    }

    .precios-listas {
        padding: 0;
        margin: 0;
        list-style: none;
        
        .lista {
            border-bottom: 1px solid $gris;

            &:nth-child(4n+1) {
                background-color: $azul;
                border-radius: 1rem;
            }
            &:nth-child(4n+3) {
                background-color: $rojo;
                border-radius: 1rem;
            }

            &:last-of-type {
                border: none;
                margin-bottom: $separacion;
            }

            p {
                font-weight: $bold;

                span {
                    font-weight: $regular;
                }
            }
        }

        a {
            border-radius: 1rem;

            &:hover {
                text-transform: uppercase;
            }
        }
    }
}