html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: $lora;
    font-size: 1.6rem;
    line-height: 1.8;
}
h1, h2, h3, h4 {
    font-family: $lora;
    margin: 0;
    font-weight: $regular;
    text-align: center;
}
h1 {
    font-size: 3.8rem;
}
h2 {
    font-size: 3.4rem;
}
h3 {
    font-size: 3rem;
}
h4 {
    font-size: 2.6rem;
}
p {
    font-size: 2rem;
    text-align: center;
    margin: 0;
}
a {
    text-decoration: none;
}
img,
picture {
    width: 100%;
    display: block;
}
.contenedor {
    width: 95%;
    max-width: 120rem;
    margin: 0 auto;
}