.pag-barberia {
    margin-bottom: $separacion;

    h2 {
        font-family: $abril;
        text-transform: uppercase;
        padding: $separacion 0;
    }

    .descripcion {
        margin-bottom: $separacion;
        @include grid(1, $separacion);

        .descr-1 {
            grid-row: 1 / 2;
        }

        .descr-2 {
            grid-row: 3 / 4;
        }

        .descr-3 {
            grid-row: 2 / 3;
            text-align: justify;
        }

        .descr-4 {
            grid-row: 4 / 5;
            text-align: justify;
        }

        @include desktop {
            @include grid(2, $separacion);
            align-items: center;

            .descr-1 {
                grid-column: 1 / 2;
                grid-row: 1 / 2;
            }
    
            .descr-2 {
                grid-column: 2 / 3;
                grid-row: 2 / 3;
            }
    
            .descr-3 {
                grid-column: 2 / 3;
                grid-row: 1 / 2;
            }
    
            .descr-4 {
                grid-column: 1 / 2;
                grid-row: 2 / 3;
            }
        }
    }
}
.barberia {
    background-image: linear-gradient(
        rgba(0, 0, 0, 0.4),
        rgba(0, 0, 0, 0.4)
      ), url(../img/banner_r&b.jpg);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    padding-bottom: 2rem;

    @include tablet {
        background-position: top center;
        padding-bottom: 2rem;
    }

    .contenedor {
        @include tablet {
            display: flex;
            justify-content: center;
            flex-wrap: wrap;
        }

        h2 {
            color: #fff;
            font-family: $lora;
            font-size: 2rem;
            font-weight: $delgada;
            padding: $separacion 0 0 0;
            border-bottom: .1rem solid #fff;
        }

        .texto-barberia {
            padding: $separacion 0;

            .nosotros {
                @include tablet {
                    @include grid(3, $separacion);
                }
            }

            .contenido-nosotros {
                color: #fff;
                margin-bottom: 2rem;

                h3 {
                    font-size: 2.5rem;
                    font-family: $lora;
                }

                p {
                    font-size: 1.6rem;
                    font-family: $lora;
                }
            }
        }

        .boton-transparente-block {
            border: 1px solid $blanco;
            margin-bottom: 2rem;
            transition: all .2s ease-in-out;

            &:hover {
                background-color: $blanco;
                color: $negro;
                text-transform: uppercase;
            }
        }
    }
}