.contenido-principal {
    display: flex;
    flex-wrap: wrap;
    margin-top: $separacion;

    @include desktop {
        justify-content: space-between;
    }

    .blog, .servicios {
        flex: 0 0 100%;
    }

    .blog {
        @include desktop {
            flex-basis: 66.6%;
        }
    }

    .servicios {
        order: -1;

        @include desktop {
            flex-basis: calc(33.3% - 4rem);
            order: 2;
        }
    }
}