.page-entrada {
    margin-top: 2rem;
    margin-bottom: 2rem;

    @include desktop {
        @include grid(2, $separacion);
        align-items: center;
    }

    h3 {
        color: $azul;
        font-weight: $black;
        border-bottom: 1px solid $azul;

        @include tablet {
            font-size: 4rem;
        }

        @include desktop {
            grid-column: 1 / 3;
            grid-row: 1 / 2;
        }
    }

    .imagen {
        @include desktop {
            grid-column: 1 / 2;
            grid-row: 2 / 3;
        }
    }

    p {
        margin: 2rem 0;
        font-size: 1.5rem;
    }
}