.admin {
    margin: $separacion auto;

    h1 {
        color: $azul;
        font-weight: $black;
    }

    .btn-volver {
        margin-bottom: $separacion;
    }

    table.servicios {
        margin-top: 4rem;
        width: 100%;
        border-spacing: 0;
        text-align: center;

        thead {
            background-color: $rojo;

            th {
                color: $blanco;
                padding: 2rem;
            }
        }

        tbody {
            td {
                border-bottom: 1px solid $gris;
                padding-bottom: 2rem;
            }
        }
    }
}

.alerta {
    padding: .5rem;
    text-align: center;
    color: $blanco;
    font-weight: $bold;
    text-transform: uppercase;
    margin: 1rem 0;

    &.error {
        background-color: $rojo;
    }

    &.exito {
        background-color: $verde;
    }
}