body {
  font-family: "Lora", serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

h1, h2, h3, h4 {
  font-family: "Lora", serif;
  margin: 0;
  font-weight: 500;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

p {
  font-size: 2rem;
  text-align: center;
  margin: 0;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 600;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-amarillo:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: 600;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-amarillo-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 600;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #CC0000;
  color: #FFFFFF;
  font-weight: 600;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  width: 100%;
}
.boton-rojo-block:hover {
  background-color: #b30000;
  cursor: pointer;
}

.boton-verde-block {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 600;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-verde-block:hover {
  background-color: #619800;
  cursor: pointer;
}

.boton-transparente-block {
  background-color: transparent;
  color: #FFFFFF;
  font-weight: 600;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-transparente-block:hover {
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
}

.boton-azul-block {
  background-color: #1485CC;
  color: #FFFFFF;
  font-weight: 600;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
}
.boton-azul-block:hover {
  background-color: #1276b5;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio,
.dark-mode .resumen-propiedad {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-propiedad .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono,
.dark-mode .resumen-propiedad .icono {
  filter: invert(100%);
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  color: #FFFFFF;
  border: 1px solid #333333;
}

/* UI */
.contenido-principal {
  display: flex;
  flex-wrap: wrap;
  margin-top: 5rem;
}
@media (min-width: 1024px) {
  .contenido-principal {
    justify-content: space-between;
  }
}
.contenido-principal .blog, .contenido-principal .servicios {
  flex: 0 0 100%;
}
@media (min-width: 1024px) {
  .contenido-principal .blog {
    flex-basis: 66.6%;
  }
}
.contenido-principal .servicios {
  order: -1;
}
@media (min-width: 1024px) {
  .contenido-principal .servicios {
    flex-basis: calc(33.3% - 4rem);
    order: 2;
  }
}

.site-header .contenedor {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.site-header .contenedor .barra {
  display: grid;
  gap: 0;
  grid-template-columns: repeat(4, 1fr);
}
.site-header .contenedor .barra a {
  grid-column: 1/4;
}
@media (min-width: 768px) {
  .site-header .contenedor .barra a {
    grid-column: 1/5;
  }
}
.site-header .contenedor .barra img {
  width: 50%;
  margin: 5rem auto 0 auto;
}
.site-header.index {
  background-image: url(../img/cabecera_r&b.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  height: auto;
  margin-bottom: 5rem;
}
.site-header.index .texto-header {
  color: #FFFFFF;
  padding: 5rem 0;
}
.site-header.index .texto-header h2 {
  font-family: "Abril Fatface", cursive;
  border-top: 1px solid #FFFFFF;
  border-bottom: 1px solid #FFFFFF;
  width: max-content;
  margin: 0 auto;
}
.site-header.general {
  background-color: #000000;
}
.site-header.general .contenedor {
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .site-header.general .contenedor {
    padding: 0;
  }
}
@media (min-width: 768px) {
  .site-header.general .contenedor .barra {
    display: flex;
    align-items: center;
  }
}
.site-header.general .contenedor .barra img {
  margin-bottom: 5rem;
}

.site-footer {
  background-color: #000000;
}
.site-footer .barra {
  padding: 5rem 0;
}
@media (min-width: 1024px) {
  .site-footer .barra {
    display: flex;
  }
}
.site-footer .barra img {
  width: 30%;
  margin: 0 auto;
}
@media (min-width: 1024px) {
  .site-footer .barra img:hover {
    transform: scale(1.2);
  }
}
.site-footer .barra .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}
.site-footer .copyright {
  color: #FFFFFF;
  font-family: "Lato", sans-serif;
  text-transform: uppercase;
  font-size: 1.5rem;
}

.navegacion {
  grid-column: 1/5;
  display: flex;
  flex-direction: column;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 0.5s linear;
  height: 0;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    justify-content: space-between;
    width: 90%;
    margin: 0 auto;
    max-width: 120rem;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  color: #FFFFFF;
  transition: 0.2s ease-in-out;
}
.navegacion a:hover {
  color: #CC0000;
  font-weight: 700;
  transform: scale(1.2);
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .navegacion a:hover {
    transform: scale(0.8);
  }
}

.mobile-menu {
  grid-column: 4/5;
  display: flex;
  align-items: flex-end;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.redes-sociales {
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .redes-sociales {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.redes-sociales .facebook {
  margin-bottom: 5rem;
}
.redes-sociales .facebook .logo-facebook {
  width: 50%;
  margin: 0 auto 2rem auto;
}
.redes-sociales .facebook .fb-page {
  display: flex;
  justify-content: center;
  margin-bottom: 2.5rem;
}
.redes-sociales .instagram .logo-instagram {
  width: 50%;
  margin: 0 auto 2rem auto;
}

.formulario {
  margin-bottom: 5rem;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 600;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}

/* Internas */
.pag-barberia {
  margin-bottom: 5rem;
}
.pag-barberia h2 {
  font-family: "Abril Fatface", cursive;
  text-transform: uppercase;
  padding: 5rem 0;
}
.pag-barberia .descripcion {
  margin-bottom: 5rem;
  display: grid;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
.pag-barberia .descripcion .descr-1 {
  grid-row: 1/2;
}
.pag-barberia .descripcion .descr-2 {
  grid-row: 3/4;
}
.pag-barberia .descripcion .descr-3 {
  grid-row: 2/3;
  text-align: justify;
}
.pag-barberia .descripcion .descr-4 {
  grid-row: 4/5;
  text-align: justify;
}
@media (min-width: 1024px) {
  .pag-barberia .descripcion {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
  .pag-barberia .descripcion .descr-1 {
    grid-column: 1/2;
    grid-row: 1/2;
  }
  .pag-barberia .descripcion .descr-2 {
    grid-column: 2/3;
    grid-row: 2/3;
  }
  .pag-barberia .descripcion .descr-3 {
    grid-column: 2/3;
    grid-row: 1/2;
  }
  .pag-barberia .descripcion .descr-4 {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}

.barberia {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url(../img/banner_r&b.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  padding-bottom: 2rem;
}
@media (min-width: 768px) {
  .barberia {
    background-position: top center;
    padding-bottom: 2rem;
  }
}
@media (min-width: 768px) {
  .barberia .contenedor {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
}
.barberia .contenedor h2 {
  color: #fff;
  font-family: "Lora", serif;
  font-size: 2rem;
  font-weight: 400;
  padding: 5rem 0 0 0;
  border-bottom: 0.1rem solid #fff;
}
.barberia .contenedor .texto-barberia {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .barberia .contenedor .texto-barberia .nosotros {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.barberia .contenedor .texto-barberia .contenido-nosotros {
  color: #fff;
  margin-bottom: 2rem;
}
.barberia .contenedor .texto-barberia .contenido-nosotros h3 {
  font-size: 2.5rem;
  font-family: "Lora", serif;
}
.barberia .contenedor .texto-barberia .contenido-nosotros p {
  font-size: 1.6rem;
  font-family: "Lora", serif;
}
.barberia .contenedor .boton-transparente-block {
  border: 1px solid #FFFFFF;
  margin-bottom: 2rem;
  transition: all 0.2s ease-in-out;
}
.barberia .contenedor .boton-transparente-block:hover {
  background-color: #FFFFFF;
  color: #000000;
  text-transform: uppercase;
}

.servicios h2 {
  font-weight: 700;
}
.servicios h3 {
  font-weight: 600;
  border-bottom: 2px solid #e1e1e1;
  margin-bottom: 2rem;
}
.servicios .precios-listas {
  padding: 0;
  margin: 0;
  list-style: none;
}
.servicios .precios-listas .lista {
  border-bottom: 1px solid #e1e1e1;
}
.servicios .precios-listas .lista:nth-child(4n+1) {
  background-color: #1485CC;
  border-radius: 1rem;
}
.servicios .precios-listas .lista:nth-child(4n+3) {
  background-color: #CC0000;
  border-radius: 1rem;
}
.servicios .precios-listas .lista:last-of-type {
  border: none;
  margin-bottom: 5rem;
}
.servicios .precios-listas .lista p {
  font-weight: 600;
}
.servicios .precios-listas .lista p span {
  font-weight: 500;
}
.servicios .precios-listas a {
  border-radius: 1rem;
}
.servicios .precios-listas a:hover {
  text-transform: uppercase;
}

.blog {
  margin: 5rem 0;
  background-image: url(../img/fondo_1.jpg);
  background-repeat: no-repeat;
  background-position: center left;
  background-size: cover;
  -webkit-box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 5px 5px 0px rgba(0, 0, 0, 0.75);
  border-radius: 0.5rem;
}
@media (min-width: 1024px) {
  .blog {
    margin-top: 0;
  }
}
.blog h2 {
  font-family: "Abril Fatface", cursive;
  color: #CC0000;
}
@media (min-width: 768px) {
  .blog .entradas {
    width: 90%;
    margin: 0 auto;
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.blog .entradas .entrada-blog {
  margin-bottom: 5rem;
  background-color: #FFFFFF;
}
@media (min-width: 768px) {
  .blog .entradas .entrada-blog {
    margin-bottom: 0;
    border: 1px solid rgba(255, 255, 255, 0.3);
    height: max-content;
  }
}
.blog .entradas .entrada-blog .imagen {
  width: 90%;
  margin: 0.5rem auto 0 auto;
