.contacto {
    h1 {
        font-weight: $regular;
        text-transform: uppercase;
        font-family: $abril;
    }

    .datos {
        margin-bottom: 2rem;
        .row {
            display: flex;
            align-items: center;
            justify-content: center;
            border-top: 1px solid $gris;
            border-bottom: 1px solid $gris;
            padding: 1rem 0;

            .icono {
                margin-right: 1rem;
                img {
                    width: 5rem;
                }
            }
            .texto {
                h2 {
                    color: $azul;
                    font-size: 2rem;
                }

                p {
                    font-size: 1.8rem;
                }
            }
        }

        @include tablet {
            @include grid(2, $separacion);
        }
    }
}